body {
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  margin: 0;
  padding: 0;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
  transition: all .3s;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card-section {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .card-section {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .card-section {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .card-section {
    grid-template-columns: repeat(4, 200px);
  }
}

.range {
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.range__reviews {
  color: #060b35;
  align-items: flex-end;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.product-cards {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  font-weight: 400;
  transition: all .3s;
}

.product-cards__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-cards__title {
  color: #060b35;
  width: 166px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-cards__code {
  color: #616070;
  align-self: self-start;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.product-cards__button {
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.product-cards__button:hover {
  box-sizing: border-box;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.product-cards:hover {
  transform: scale(1.2);
}

.product-cards:hover .product-cards__title {
  color: #34568b;
}

.price {
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  margin-top: 24px;
  display: flex;
}

.price__title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.price__amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}
